.cards__title {
    color: black;
    font-size: 20px;
    
}

.cards__container {
    display: flex;
    flex-flow: column;
    align-items: center;
    max-width: 100%;
    width: 100%;
    margin: 0 auto;
    
    
  }
  
  .cards__wrapper {
    position: relative;
    margin: 1px 0 70px;
    
  }
  
  .cards__items {
    margin-bottom: 24px;
    width: 115%;
    padding: 0%;
    padding-right: 15%;
  }
  
  .cards__item {
    display: flex;
    flex: 1;
    margin: 0 1rem;
    border-radius: 10px;
    border: 2px solid white;

  }
  
  .cards_item:hover{
    background-color: #1f98f4; 
  color: white;
  border-radius: 10px;
    
  }


  .cards__item__link {
    display: flex;
    flex-flow: column;
    width: 100%;
    box-shadow: 0 6px 20px rgba(2, 90, 255, 0.17);
    -webkit-filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    filter: drop-shadow(0 6px 20px rgba(56, 125, 255, 0.017));
    border-radius: 10px;
    overflow: hidden;
    text-decoration: none;
    background: white
  }
  
  .cards__item__pic-wrap {
    position: relative;
    width: 100%;
    padding-top: 67%;
    overflow: hidden;
    
  }
  
  .fade-img {
    animation-name: fade-img;
    animation-duration: 2s;
  }
  
  .cards__item__pic-wrap::after {
    position: absolute;
    bottom: 0;
    margin-left: 10px;
    padding: 6px 8px;
    max-width: calc((100%) - 60px);
    font-size: 12px;
    font-weight: 700;
    color: #fff;
    background-color: #1f98f4;
    box-sizing: border-box;
  }
  
  .cards__item__img {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 100%;
    max-width: 100%;
    height: 100%;
    max-height: 100%;
    object-fit: cover;
    transition: all 0.2s linear;
    
  }
  

  .cards__item__img:hover {
    transform: scale(1.1);
    
  }
  
  .cards__item__info {
    padding: 20px 30px 30px;
  }
  
  .cards__item__text {
    color: #252e48;
    font-size: 18px;
    line-height: 24px;
  }
  
  @media only screen and (min-width: 600px) {
    .content__blog__container {
      width: 84%;
    }
    .cards__items {
      display: flex;
    }
    .cards__item {
      margin-bottom: 2rem;
    }

  }
  
 
